<template>
  <div id="app">
    <h1>New Entry</h1>
    <div class="row">
      <div class="col-md-5"></div>
      <div class="col-md-2"><input type="text" class="form-control" v-model="referenceNumber" placeholder="Enter Reference Number" @input="validateInput"/></div>
      <div class="col-md-5"></div>
    </div>
    

    <div v-if="isValidReference">
      <div  class="dropzone"
        @dragover.prevent="dragOver"
        @dragenter.prevent="dragEnter"
        @dragleave.prevent="dragLeave"
        @drop.prevent="handleDrop">
        Please Upload Commercial Invoices
        <input type="file" multiple @change="handleFileUpload" style="display: none;">
        <button type="button" class="btn btn-primary" onclick="document.querySelector('input[type=file]').click()">Choose Files</button>
        <button type="button" class="btn btn-primary upload-button" @click="uploadFiles">Upload Files AWS</button>
        <button type="button" class="btn btn-primary upload-button" @click="uploadFilesMicrosoft">Upload Files GABRIEL</button>
      </div>
      <!-- <div v-if="selectedFiles.length"> -->
      <div >
        
        <ul >
          <li v-for="(file, index) in selectedFiles" :key="index">{{ file.name }}</li>
        </ul>
        <CustomsEntryForm :customsEntryForm = "customsEntryForm" @update-customs-entry-form="customsEntryForm = $event"/>
      </div>
      
    </div>
    
    
    <div v-for="(data, dataIndex) in invoiceDataArray" :key="dataIndex">
      <h2>Invoice Data #{{ dataIndex + 1 }}</h2>
      <h3>Header Information</h3>
      <div class="form-row">
        <div class="row">
          <div class="col-md-3">
            <label for="InvoiceNumber"><strong>Invoice Number</strong></label>
            <input type="text" v-model="data.invoice_id" class="form-control" id="InvoiceNumber" placeholder="Enter Invoice Number">
          </div>
          <div class="col-md-3">
            <label for="SupplierName"><strong>Supplier Name</strong></label>
            <input type="text" v-model="data.vendor_name" class="form-control" id="SupplierName" placeholder="Enter Supplier Name">
          </div>
          <div class="col-md-2">
            <label for="InvoiceDate"><strong>Invoice Date</strong></label>
            <input type="date" v-model="data.invoice_date" class="form-control" id="InvoiceDate">
          </div>
          
          <div class="col-md-2">
            <label for="invoiceCurrency" class="form-label"><strong>Invoice Currency</strong></label>
            <select class="form-select" v-model="data.currency">
              <option value="" disabled>Select a Currency</option>
              <option v-for="currency in currencies" :key="currency.code" :value="currency.code">{{ currency.code }} - {{ currency.name }}</option>
            </select>
          </div>
          <div class="col-md-2">
            <label for="rateOfExchange" class="form-label"><strong>Rate of Exchange</strong></label>
            <input type="number" class="form-control" id="rateOfExchange" v-model="data.rate_of_exchange" required>
          </div>
        </div>
      </div>

      

      <h3>Line Details</h3>
      <table>
        <thead>
          <tr>
            <th>Line #</th>
            <th>Product Code</th>
            <th>Description</th>
            <th>THN Code</th>
            <th>Unit Quantity</th>
            <th>Unit Price</th>
            <th>Extension Price</th>
            <th>Country of Origin</th>
            <th>CPC Code</th>
            <th>NPC Code</th>
            <th>VAT Applicable</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, itemIndex) in data.invoice_items" :key="itemIndex">
            <td>{{ itemIndex + 1 }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'product_code', $event.target.innerText)">{{ item.product_code || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'description', $event.target.innerText)">{{ item.description || 'N/A' }}</td>
            <td contenteditable="true" @input="handleInput($event, itemIndex, 'thn_code')" @blur="updateItem(data, itemIndex, 'thn_code', $event.target.innerText)">{{ item.thn_code || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'quantity', $event.target.innerText)" :style="naStyle(item.quantity)">{{ item.quantity || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'unit_price', $event.target.innerText)" :style="naStyle(item.unit_price)">{{ item.unit_price || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'amount', $event.target.innerText)" :style="extensionStyle(item)">{{ item.amount || 'N/A' }}</td>
            <td>
              <select class="form-select" v-model="item.country_of_origin" id="country">
                <option disabled value="">Please select one</option>
                <option v-for="country in countries" :key="country.code" :value="country.code">
                  {{ country.code }}
                </option>
              </select>
            </td>
            <td>
              <select class="form-select" v-model="item.cpcCode" id="cpcCode">
                <option disabled value="">Please select one</option>
                <option v-for="option in availableCPCCodes" :key="option.cpcId" :value="option.cpcId">{{ option.code }}</option>
              </select>
            </td>
            <td>
              <select class="form-select" v-model="item.npcCode" id="npcCode">
                <option disabled value="">Please select one</option>
                <option v-for="option in npcCodeOptions" :key="option" :value="option">{{ option }}</option>
              </select>
            </td>
            <td>
              <div class="form-check form-switch">
                <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" v-model="item.vatApplicable">
              </div>
            </td>
            <td><button class="btn btn-danger" @click="deleteRow(data, itemIndex)"><i class="fa fa-trash"></i></button></td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="7"><button @click="addNewLine(data)">Add New Line</button></td>
          </tr>
        </tfoot>
      </table>

      <h3>Footer Information</h3>
      <table>
        <thead>
          <tr>
            <th>Invoice Total</th>
            <th>Inland</th>
            <th>Insurance</th>
            <th>Other Charges</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td contenteditable="true" @blur="updateData(data, 'invoice_total', $event.target.innerText)" :style="naStyle(data.invoice_total)">{{ data.invoice_total || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'inland', $event.target.innerText)" :style="naStyle(data.inland)">{{ data.inland || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'insurance', $event.target.innerText)" :style="naStyle(data.insurance)">{{ data.insurance || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'otherCharges', $event.target.innerText)" :style="naStyle(data.otherCharges)">{{ data.otherCharges || 'N/A' }}</td>
            
          </tr>
        </tbody>
      </table>

      <h3>Calculated Check</h3>
      <table>
        <thead>
          <tr>
            <th>Total Extension Prices</th>
            <th>Number of Line Items</th>
            <th>Matches Invoice Total?</th>
          </tr>
        </thead>
        <tbody>
          <tr :style="checkInvoiceTotal(data)">
            <td>{{ calculateTotalExtensionPrice(data.invoice_items) }}</td>
            <td>{{ data.invoice_items.length }}</td>
            <td>{{ calculateTotalExtensionPrice(data.invoice_items) === parseFloat(data.invoice_total).toFixed(2) ? 'Yes' : 'No' }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    
    <div v-if="selectedFiles.length">
      <h3>Entry Summary</h3>
      <table>
        <thead>
          <tr>
            <th>Number of Lines Read</th>
            <th>Total Invoices Value</th>
          </tr>
        </thead>
       <tbody>
        <tr>
          <td>{{ this.totalNumberOfLines }}</td>
          <td>{{ this.totalFOBValue }}</td>
        </tr>
        <tr >
          <td><button type="button" class="btn btn-primary" :disabled="entryButtonDisabled" @click="createInvoices(invoiceDataArray)">Save Entry</button></td>
          <td><button type="button" class="btn btn-primary" :disabled="thnCodesCompleted" @click="generateWorkSheet">Generate WorkSheet</button></td>
        </tr>
       </tbody>
      </table>
    </div>
    
  </div>
</template>

<script>
import * as DatabaseConnector from '@/composables/DatabaseConnector';
import * as Utilities from '@/composables/Utilities';
import * as SweetAlertConnector from '../composables/SweetAlertConnector';
import * as PackageTypes from '@/composables/PackageTypes';
import * as Currency from '@/composables/Currency';
import * as Configuration from '@/composables/BrokerConfiguration';

import CustomsEntryForm from './CustomsEntry/CustomsEntryForm.vue';

export default {
  /* eslint-disable */
  name: 'App',
  components: {
    CustomsEntryForm
  },
  data() {
    return {
      referenceNumber: '',
      selectedFiles: [],
      invoiceDataArray: [],
      isDragOver: false,
      isValidReference: false,

      waybillNumber: '',
      waybillDate: new Date().toISOString().substr(0, 10),
      modeOfTransport: '',

      freightType: '',
      marksAndNumbers: 'As Addressed',
      countryOfOrigin: '',

      cpcCode: '4000',
      npcCode: '0000',
      kindOfPackage: 'CT',
      numberOfPackages: '',

      consignee: '',
      shipper: '',
      supplier: '',
      totalCargoValue: 0.0,
      totalNumberOfLines: 0,
      freightCharge: 0.0,
      rateOfExchange: 1.0,
      netWeight: 0.0,
      grossWeight: 0.0,

      editableContent: '0',

      countries: [],
      modeOfTransportOOptions: ['Air', 'Ocean'],
      freightTypeOptions: ['FCL', 'LCL', 'Bulk', 'Break Bulk'],
      cpcCodeOptions: ['4000'],
      npcCodeOptions: ['0000'],
      packageTypeOptions: [
      ],
      currencies: [],
      showModeofTransportDropdown: false,

      customsEntryForm: {
        exporters: [],
            importers: [],
            vessels: [],
            ports: [],
            declarants: [],
            importerId: '',
            exporterId: '',
            importerName: '',
            exporterName: '',
            importerTaxIdentificationNumber: '',
            exporterTaxIdentificationNumber: '',
            declarantId: '',
            declarantTaxIdentificationNumber: '',
            modeOfTransport: '',
            freightType: '',
            waybillNumber: '',
            waybillDate: '',
            vesselId: '',
            vesselName: '',
            portOfDischargeId: '',
            portOfDischargeName: '',
            netWeight: 0.0,
            grossWeight: 0.0,
            numberOfPackages: 0,
            packageType: '',
            packageTypes: [],
            marksAndNumbers: 'As Addressed',
            totalCargoValue: 0.0,
            freightAmount: 0.0,
            rateOfExchange: 0.0,
            regimeType: '',
            regimeTypeCode: '',
            regimeTypeOptions: [],
            depositAmount: 0.0,
            containerCharges: 0.0,
            additionalCharges: 0.0,
            localFee: 0.0,
            countryOfOrigin: '',
      }

    };
  },
  watch: {
    'customsEntryForm.countryOfOrigin'(newCountry, oldCountry) {
      this.invoiceDataArray.forEach(invoice => {
        invoice.invoice_items.forEach(item => {
          if (!item.country_of_origin || item.country_of_origin.trim().length === 0) {
            item.country_of_origin = newCountry;
          }
        });
      });
    }
  },

  setup() {
    return {
    };
  },
  mounted() {
    this.fetchCountries();
    this.packageTypeOptions = PackageTypes.usePackageTypes();
    this.currencies = Currency.getCurrencyList();
    this.updateEntryFields('marksAndNumbers', 'As Addressed');
    this.getTotalNumberOfLines();
    this.getRegimeTypes();
  },
  computed: {
    thnCodesCompleted() {
      return this.invoiceDataArray.some(invoice => {
        return invoice.invoice_items.some(line => !line.thn_code || line.thn_code.trim() === '');
      });
    },
    totalFOBValue() {
      return this.invoiceDataArray.reduce((acc, invoice) => acc + parseFloat(invoice.invoice_total), 0).toFixed(2);
    },
    entryButtonDisabled(){
      return !(this.hasInvoiceNumber || this.waybillNumber.trim().length > 0 || this.consignee.trim().length > 0) 
    },
    hasInvoiceNumber(){
      for(let invoice of this.invoiceDataArray){
        if(invoice.invoice_id && invoice.invoice_id.trim().length > 0){
          console.log("Invoice Number Found")
          return true
        }
      }
      return false;
    },
    availableCPCCodes(){
      if(this.customsEntryForm.regimeTypeCode !== undefined && this.customsEntryForm.regimeTypeCode !== null){
        const regimeData = this.customsEntryForm.regimeTypeOptions.find(regime => regime.regimeTypeCode  === this.customsEntryForm.regimeTypeCode);
        if(regimeData){
          return regimeData.cpcCodes
        } else return []
      }
      else{
        return this.customsEntryForm.regimeTypeOptions.flatMap(regime => regime.cpcCodes)
      }
    }
  },
  methods: {
    setDefaults(){
      const regimeData = this.customsEntryForm.regimeTypeOptions.find(regime => regime.regimeTypeCode  === Configuration.defaultRegimeTypeCode);
      this.customsEntryForm.regimeType = regimeData.regimeTypeId;
      console.log("Regime Data: ", regimeData)
    },
    async getRegimeTypes(){
      DatabaseConnector.fetchCpcCodes('TT').then(data => {
        this.customsEntryForm.regimeTypeOptions = data;
        const regimeData = data.find(regime => regime.regimeTypeCode  === Configuration.getConfigurations().defaultRegimeType);
        this.customsEntryForm.regimeType = regimeData.regimeTypeId;
      });
    },
    setInvoiceLineWorksheetData(){
      let promises = [];
      this.invoiceDataArray.forEach(invoice => {
        invoice.invoice_items.forEach(item => {
          item.cpcCodeName = this.availableCPCCodes.find(cpc => cpc.cpcId === item.cpcCode)?.code
          const promise = DatabaseConnector.getCommodityCharges(item.thn_code.replace(/\./g, '')).then(chargesResponse => {
            let formattedCharges = Utilities.formatCharges(chargesResponse.charges)
            item.commodity = {
              code: item.thn_code.replace(/\./g, ''),
              id: chargesResponse.id,
              charges: formattedCharges
            }
            // console.log("Commodity Charges: ", item.commodity)
          })
          promises.push(promise)
        })
      })

      return Promise.all(promises)
    },
    generateWorkSheet(){
      console.log("Generating WorkSheet")
      this.setInvoiceLineWorksheetData();
      const worksheetObject = Utilities.formatWorkSheetData(this.referenceNumber, 'TT', 'Ramnarine Rampersad', 'BR0285', this.customsEntryForm.regimeTypeCode, this.customsEntryForm.importerName, this.customsEntryForm.importerTaxIdentificationNumber, this.customsEntryForm.exporterName, this.customsEntryForm.exporterTaxIdentificationNumber, this.customsEntryForm.vesselName, this.customsEntryForm.portOfDischargeName, this.customsEntryForm.depositAmount, this.customsEntryForm.containerCharges, this.customsEntryForm.additionalCharges, this.customsEntryForm.localFee);
      const invoiceSummary = Utilities.formatInvoiceData(this.invoiceDataArray, this.customsEntryForm.freightAmount, this.totalFOBValue);
      worksheetObject.invoices = invoiceSummary;
      console.log("Worksheet Object: ", worksheetObject)
      DatabaseConnector.generateWorkSheet(worksheetObject).then((worksheetResponse) => {
        SweetAlertConnector.closeCurrentAlert();
        SweetAlertConnector.showSuccessAlert('Worksheet Generated Successfully!');
        const fileURL = window.URL.createObjectURL(new Blob([worksheetResponse], { type: 'application/pdf' }));
        const fileLink = document.createElement('a');
        const fileName = `Worksheet - ${this.entryDetails?.entry_number}.pdf`;
        fileLink.href = fileURL;
        fileLink.setAttribute('download', fileName);
        document.body.appendChild(fileLink);
        fileLink.click();
        fileLink.remove();
      }).catch(error => {
        console.error('Failed to generate worksheet:', error);
        SweetAlertConnector.showErrorAlert('Failed to generate worksheet!');
      });

    },
    convertAWSData(invoices){
      return invoices.map(invoiceArray => {
        return invoiceArray.map(invoice => {
          const summary = invoice.summary_fields;
          const items = invoice.line_items.map(line => {
            // Initialize a temporary storage for the fields
            const tempItem = {
              product_code: '',
              quantity: '',
              unit_price: '',
              description: '',
              amount: ''
            };
    
            // Loop through each object in the line array and assign values based on field_type
            line.forEach(item => {
              switch (item.field_type) {
                case 'PRODUCT_CODE':
                  tempItem.product_code = item.cell_data.text;
                  break;
                case 'QUANTITY':
                  tempItem.quantity = item.cell_data.text;
                  break;
                case 'UNIT_PRICE':
                  tempItem.unit_price = item.cell_data.text;
                  break;
                case 'ITEM':
                  tempItem.description = item.cell_data.text;
                  break;
                case 'PRICE':
                  tempItem.amount = item.cell_data.text;
                  break;
              }
            });
    
            return tempItem;
          });
    
          return {
            vendor_name: summary.VENDOR_NAME,
            subtotal: summary.SUBTOTAL,
            invoice_total: summary.TOTAL,
            customer_name: summary.RECEIVER_NAME,
            invoice_date: summary.INVOICE_RECEIPT_DATE,
            invoice_id: summary.INVOICE_RECEIPT_ID,
            invoice_items: items
          };
        });
      }).flat();
    },
    formatInvoiceData(){
      const allcpcCodes = this.customsEntryForm.regimeTypeOptions.flatMap(regime => regime.cpcCodes)
      const defaultCPCId = allcpcCodes.find(cpc => cpc.code === Configuration.getConfigurations().defaultCPCCode).cpcId
      console.log("All CPC Codes: ", allcpcCodes)
      console.log("Default CPC Code: ", defaultCPCId)
      this.invoiceDataArray.forEach(invoice => {
        invoice.invoice_total = this.parseFloatFromString(invoice.invoice_total)
        invoice.inland = this.parseFloatFromString(invoice.inland)
        invoice.insurance = this.parseFloatFromString(invoice.insurance)
        invoice.otherCharges = this.parseFloatFromString(invoice.otherCharges)
        invoice.final_total = this.parseFloatFromString(invoice.final_total)
        invoice.invoice_items.forEach(item => {
          item.country_of_origin = ""
          item.quantity = this.parseFloatFromString(item.quantity)
          item.unit_price = this.parseFloatFromString(item.unit_price).toFixed(2)
          item.amount = this.parseFloatFromString(item.amount).toFixed(2)
          item.vatApplicable = true
          item.cpcCode = defaultCPCId
          item.npcCode = "0000"
          item.thn_code = ""
          // item.vat_applicable = item.vat_applicable === 'Yes'
        })
      })
    },
    getTotalNumberOfLines(){
      let total = 0;
      for(let invoice of this.invoiceDataArray){
        total += invoice.invoice_items.length
      }
      this.totalNumberOfLines = total;
    },
    calculateTotalCargoValue(){
      let total = 0;
      for(let invoice of this.invoiceDataArray){
        total += this.parseFloatFromString(invoice.invoice_total)
      }
      this.customsEntryForm.totalCargoValue = total;
    },
    validateIntegerInput(event) {
      // Get the entered text
      let newText = event.target.innerText.trim();

      // Check if the entered text is a valid integer
      if (/^\d+$/.test(newText)) {
        // Update the content if it's a valid integer
        this.editableContent = newText;
      } else {
        // Revert to the previous content if it's not a valid integer
        event.target.innerText = this.editableContent;
      }
      this.moveCursorToEnd(event.target);
    },
    async fetchCountries() {
      try {
        const response = await fetch('https://restcountries.com/v3.1/all');
        const data = await response.json();
        this.countries = data.map(country => ({
          name: country.name.common,
          code: country.cca2
        }));
      } catch (error) {
        console.error('Failed to fetch countries:', error);
      }
    },
    updateEntryFields(fieldName, value) {
      this[fieldName] = value;
    },
    selectOption(option) {
      this.$refs.editableDiv.innerText = option;
      this.showModeofTransportDropdown = false;
    },
    verifyData(data){
      if(data === undefined || data === null || data === ''){
        return false
      }
      return true
    },
    validateData(){
      console.log("Importer ID: ", this.verifyData(this.customsEntryForm.importerId))
      if(!this.verifyData(this.customsEntryForm.importerId)){
        SweetAlertConnector.showErrorAlert('Please select an importer');
        return false
      }
      if(!this.verifyData(this.customsEntryForm.exporterId)){
        SweetAlertConnector.showErrorAlert('Please select an exporter');
        return false
      }
      return true
    },
    async createInvoices(invoiceDataArray){
      if(!this.validateData()){
        return
      }
      const entryData = {
        referenceNumber: this.referenceNumber,
        waybillNumber: this.customsEntryForm.waybillNumber,
        waybillDate: this.customsEntryForm.waybillDate,
        modeOfTransport: this.customsEntryForm.modeOfTransport,
        freightType: this.customsEntryForm.freightType,
        marksAndNumbers: this.customsEntryForm.marksAndNumbers,
        countryOfOrigin: this.customsEntryForm.countryOfOrigin,
        kindOfPackage: this.customsEntryForm.packageType,
        numberOfPackages: this.customsEntryForm.numberOfPackages,
        totalCargoValue: this.customsEntryForm.totalCargoValue,
        freightCharge: this.customsEntryForm.freightAmount,
        rateOfExchange: this.customsEntryForm.rateOfExchange,
        netWeight: this.customsEntryForm.netWeight,
        grossWeight: this.customsEntryForm.grossWeight,
        invoiceList: invoiceDataArray,

        declarant: this.customsEntryForm.declarantId,
        importer: this.customsEntryForm.importerId,
        shipper: this.customsEntryForm.exporterId,
        vessel: this.customsEntryForm.vesselId,
        portOfDischarge: this.customsEntryForm.portOfDischargeId,
        regimeType: this.customsEntryForm.regimeType,
        deposit: this.customsEntryForm.depositAmount,
        containerCharges: this.customsEntryForm.containerCharges,
        additionalCharges: this.customsEntryForm.additionalCharges,
        localFee: this.customsEntryForm.localFee,
      };
      try {
        SweetAlertConnector.showLoadingAlert('Saving your Entry...');
        DatabaseConnector.createCommercialInvoice(entryData).then((response) => {
          console.log('Entry created successfully:', response);
          this.redirectToEntryDetails(response.entryId);
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('Entry created successfully');
          // Optionally reset the form or redirect the user
        }).catch((error) => {
          console.error('Failed to create the Entry:', error);
          SweetAlertConnector.showErrorAlert('Failed to create the Entry. Please try again.');
        });
        
      } catch (error) {
        console.error('Failed to create the invoice:', error);
        SweetAlertConnector.showErrorAlert('Failed to create the Entry. Please try again.');
      }
    },
    redirectToEntryDetails(entryId){
      this.$router.push({ name: 'EntryDetails', params: { entryId } });
    },
    handleInput(event, itemIndex, field) {
      const value = event.target.innerText;
      // Remove any existing dots and non-numeric characters
      const sanitizedValue = value.replace(/[^0-9]/g, '');
      // Format the value as XXXX.XX.XX
      const formattedValue = this.formatValue(sanitizedValue);
      // Update the text content of the cell
      event.target.innerText = formattedValue;
      this.moveCursorToEnd(event.target);
      // Update the item data after handling the input
    },
    formatValue(value) {
      let formattedValue = '';
      // Insert dots after the first four characters and after the next two characters
      for (let i = 0; i < value.length; i++) {
        if (i === 4 || i === 6) {
          formattedValue += '.';
        }
        formattedValue += value[i];
      }
      return this.truncateString(formattedValue);
    },
    truncateString(str) {
      if (str.length > 13) {
        return str.substring(0, 13);
      }
      return str;
    },
    moveCursorToEnd(element) {
      const range = document.createRange();
      const selection = window.getSelection();
      range.selectNodeContents(element);
      range.collapse(false); // Collapse range to end
      selection.removeAllRanges();
      selection.addRange(range);
    },
    validateInput() {
      this.isValidReference = this.referenceNumber.trim().length > 0;
    },
    handleFileUpload(event) {
      if (!this.isValidReference) return;
      this.selectedFiles = Array.from(event.target.files);
    },
    dragOver(event) {
      if (!this.isValidReference) return;
      this.isDragOver = true;
    },
    dragEnter(event) {
      if (!this.isValidReference) return;
      this.isDragOver = true;
    },
    dragLeave(event) {
      if (!this.isValidReference) return;
      this.isDragOver = false;
    },
    uploadFilesMicrosoft(){
      if (!this.isValidReference || this.selectedFiles.length === 0) {
        alert("Please enter a valid reference number and select files before uploading.");
        return;
      }
      const formData = new FormData();
      formData.append('reference', this.referenceNumber);
      this.selectedFiles.forEach(file => {
        formData.append('files', file);
      });
      SweetAlertConnector.showLoadingAlert('Reading your invoice...');

      fetch('https://gabrielle-flask.azurewebsites.net/read_vendor_invoice', {
        method: 'POST',
        body: formData
      })
      .then(response => response.json())
      .then(data => {
          console.log('Success:', data);
          this.invoiceDataArray = data;
          this.formatInvoiceData()
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('Invoice data read successfully.');
          this.calculateTotalCargoValue()
          this.getTotalNumberOfLines()
      })
      .catch(error => {
        console.error('Error:', error);
        this.invoiceDataArray = [];
      });
    },
    uploadFiles() {
      if (!this.isValidReference || this.selectedFiles.length === 0) {
        alert("Please enter a valid reference number and select files before uploading.");
        return;
      }
      const formData = new FormData();
      formData.append('reference', this.referenceNumber);
      this.selectedFiles.forEach(file => {
        formData.append('commercialInvoice', file);
      });
      SweetAlertConnector.showLoadingAlert('Reading your invoice...');

      fetch('https://mary-staging.whitestone-99c262a0.eastus.azurecontainerapps.io//analyze-document', {
        method: 'POST',
        body: formData
      })
      .then(response => response.json())
      .then(data => {
          console.log('Success:', data);
          this.invoiceDataArray = this.convertAWSData(data);
          this.formatInvoiceData()
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('Invoice data read successfully.');
          this.calculateTotalCargoValue()
          this.getTotalNumberOfLines()
      })
      .catch(error => {
        console.error('Error:', error);
        this.invoiceDataArray = [];
      });
    },
    calculateTotalExtensionPrice(items) {
      return items.reduce((acc, item) => acc + this.parseFloatFromString(item.amount || 0), 0).toFixed(2);
    },
    updateData(data, field, value) {
      data[field] = value;
    },
    updateItem(data, index, field, value) {
      data.invoice_items[index][field] = value;
    },
    deleteRow(data, index) {
      data.invoice_items.splice(index, 1);
    },
    addNewLine(data) {
      const newItem = {
        product_code: '',
        description: '',
        thn_code: '',
        quantity: '',
        unit_price: '',
        amount: ''
      };
      data.invoice_items.push(newItem);
    },
    extensionStyle(item) {
      const expected = (parseFloat(item.unit_price) * parseFloat(item.quantity)).toFixed(2);
      const actual = parseFloat(item.amount).toFixed(2);
      if ([item.unit_price, item.quantity, item.amount].some(v => [null, undefined, '', false].includes(v))) {
        // return { backgroundColor: 'yellow', color: 'black' };
        return {"font-weight": 'bold', "color": '#B8860B'};
      } else if (expected !== actual) {
        // return { backgroundColor: 'red', color: 'white' };
        return {"font-weight": 'bold', "color": 'red'};
      }
      return {};
    },
    naStyle(value) {
      if ([null, undefined, '', false].includes(value)) {
        // return { backgroundColor: 'yellow', color: 'black' };
        return {"font-weight": 'bold', "color": '#B8860B'};
      }
      return {};
    },
    checkInvoiceTotal(data) {
      const totalExtensions = this.calculateTotalExtensionPrice(data.invoice_items);
      const invoiceTotal = parseFloat(data.invoice_total || 0).toFixed(2);
      return totalExtensions !== invoiceTotal ? { backgroundColor: 'red', color: 'white' } : {};
    },
    parseFloatFromString(inputString) {

        if (!inputString || inputString === undefined || inputString === null) {
            return 0;
        }

        if(typeof inputString === 'number') {
            return inputString;
        }
        // Remove non-numeric characters except for period (.)
        let numericString = inputString.replace(/[^0-9.]/g, '');

        // Parse the numeric string as a float
        let floatValue = parseFloat(numericString);

        return floatValue;
    }
  }
}
</script>


