  <div v-if="dataRetrieved">
    <h1>Entry Details</h1>
  <h2>Reference Number: {{ this.entryDetails?.entry_number }}</h2>
  <CustomsEntryForm ref="myCustomsForm" :customsEntryForm = "customsEntryForm" @update-customs-entry-form="customsEntryForm = $event"/>
