                {{ country.code }} - {{ country.name }}
              </option>
            </select>
          </td>
        </tr>
      </tbody>
    </table> -->

    <!-- End of Entry Information -->

    <!-- Commercial Invoices Table -->
    <div v-if="dataRetrieved">
      <h3>Commercial Invoices</h3>
      <div v-for="(data, dataIndex) in commercialInvoices" :key="dataIndex">
        <h2>Invoice Data #{{ dataIndex + 1 }}</h2>
        <h3>Header Information</h3>
        <!-- <table>
          <thead>
            <tr>
              <th>Invoice #</th>
              <th>Invoice Date</th>
              <th>Supplier Name</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_number', $event.target.innerText)">{{ data.invoice_number || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_date', $event.target.innerText)">{{ data.invoice_date || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'vendor_name', $event.target.innerText)">{{ data.supplier_name || 'N/A' }}</td>
            </tr>
          </tbody>
        </table> -->

        <div class="form-row">
          <div class="row">
            <div class="col-md-3">
              <label for="InvoiceNumber" class="form-label"><strong>Invoice Number</strong></label>
              <input type="text" v-model="data.invoice_number" class="form-control" id="InvoiceNumber" placeholder="Enter Invoice Number">
            </div>
            <div class="col-md-3">
              <label for="SupplierName" class="form-label" ><strong>Supplier Name</strong></label>
              <input type="text" v-model="data.vendor_name" class="form-control" id="SupplierName" placeholder="Enter Supplier Name">
            </div>
            <div class="col-md-2">
              <label for="InvoiceDate" class="form-label"><strong>Invoice Date</strong></label>
              <input type="date" v-model="data.invoice_date" class="form-control" id="InvoiceDate">
            </div>
            <div class="col-md-2">
              <label for="invoiceCurrency" class="form-label"><strong>Invoice Currency</strong></label>
              <select class="form-select" v-model="data.currency">
                <option value="" disabled>Select a Currency</option>
                <option v-for="currency in currencies" :key="currency.code" :value="currency.code">{{ currency.code }} - {{ currency.name }}</option>
              </select>
            </div>
            <div class="col-md-2">
              <label for="rateOfExchange" class="form-label"><strong>Rate of Exchange</strong></label>
              <input type="number" class="form-control" id="rateOfExchange" v-model="data.rate_of_exchange" required>
            </div>
          </div>
        </div>
        <h3>Line Details</h3>
        <table>
          <thead>
            <tr>
              <th>Line Number</th>
              <th>Product Code</th>
              <th>Description</th>
              <th>THN Code</th>
              <th>Unit Quantity</th>
              <th>Unit Price</th>
              <th>Extension Price</th>
              <th>Country of Origin</th>
              <th>CPC Code</th>
              <th>NPC Code</th>
              <th>VAT Applicable</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, itemIndex) in data.lines" :key="itemIndex">
              <td>{{ itemIndex + 1 }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item, 'product_code', $event.target.innerText)">{{ item.product_code || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item, 'description', $event.target.innerText)">{{ item.description || 'N/A' }}</td>
              <td contenteditable="true" @input="handleInput($event)" @blur="updateInvoiceData(item, 'tariff_code', $event.target.innerText)">{{ item.tariff_code || 'N/A' }} </td>
              <td contenteditable="true" @blur="updateInvoiceData(item, 'quantity', $event.target.innerText)" :style="naStyle(item.quantity)">{{ item.quantity || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item,'unit_price', $event.target.innerText)" :style="naStyle(item.unit_price)">{{ item.unit_price || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item,'extension_price', $event.target.innerText)" :style="extensionStyle(item)">{{ item.extension_price || 'N/A' }}</td>
              <td>
                <select class="form-select" v-model="item.country_of_origin" id="country">
                  <option disabled value="">Please select one</option>
                  <option v-for="country in countries" :key="country.code" :value="country.code">
                    {{ country.code }}
                  </option>
                </select>
              </td>
              <td>
                <select class="form-select" v-model="item.cpcCode" id="cpcCode">
                  <option disabled value="">Please select one</option>
                  <option v-for="option in availableCPCCodes" :key="option.cpcId" :value="option.cpcId">{{ option.code }}</option>
                </select>
              </td>
              <td>
                <select class="form-select" v-model="item.npcCode" id="npcCode">
                  <option disabled value="">Please select one</option>
                  <option v-for="option in npcCodeOptions" :key="option" :value="option">{{ option }}</option>
                </select>
              </td>
              <td>
                <div class="form-check form-switch">
                  <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" v-model="item.vatApplicable">
                </div>
              </td>
              <td><button class="btn btn-danger" @click="deleteRow(data, itemIndex)"><i class="fa fa-trash"></i></button></td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="7"><button @click="addNewLine(data)">Add New Line</button></td>
            </tr>
          </tfoot>
        </table>
        <h3>Footer Information</h3>
        <table>
          <thead>
            <tr>
              <th>Invoice Total</th>
              <th>Inland</th>
              <th>Insurance</th>
              <th>Other Charges</th>
              <th>Final Total</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_total', $event.target.innerText)" :style="naStyle(data.invoice_total)">{{ data.invoice_total || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'inland', $event.target.innerText)" :style="naStyle(data.inland)">{{ data.inland || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'insurance', $event.target.innerText)" :style="naStyle(data.insurance)">{{ data.insurance || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'other_charges', $event.target.innerText)" :style="naStyle(data.other_charges)">{{ data.other_charges || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'final_total', $event.target.innerText)" :style="naStyle(data.final_total)">{{ data.final_total || 'N/A' }}</td>
            </tr>
          </tbody>
        </table>

        <h3>Calculated Check</h3>
        <table>
          <thead>
            <tr>
              <th>Total Extension Prices</th>
              <th>Number of Line Items</th>
              <th>Matches Invoice Total?</th>
            </tr>
          </thead>
          <tbody>
            <tr :style="checkInvoiceTotal(data)">
              <td>{{ calculateTotalExtensionPrice(data.lines) }}</td>
              <td>{{ data.lines.length }}</td>
              <td>{{ calculateTotalExtensionPrice(data.lines) === parseFloat(data.invoice_total).toFixed(2) ? 'Yes' : 'No' }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div>
      <table>
        <thead>
          <tr>
            <th>Number of Lines Read</th>
            <th>Total Invoices Value</th>
          </tr>
        </thead>
       <tbody>
        <tr>
          <td>{{ this.totalNumberOfLines }}</td>
          <td>{{ this.totalFOBValue }}</td>
        </tr>
        </tbody>
      </table>
      <table>
        <tr>
          <td><button type="button" class="btn btn-success" :disabled="isGenerateCsvDisabled" @click="generateCSV">Generate CSV File</button></td>
          <td><button type="button" class="btn btn-primary" @click="saveData">Save Entry</button></td>
          <td><button type="button" class="btn btn-primary" @click="goToCustomsDeclarationPage(this.entryId)">Generate Valuation Form</button></td>
          <td><button type="button" class="btn btn-primary" :disabled="isGenerateCsvDisabled" @click="generateWorkSheet" >Generate Worksheet</button></td>
          <td><button type="button" class="btn btn-primary" @click="generateDepositForm">Generate Deposit Form</button></td>
          <td><button type="button" class="btn btn-primary" @click="generateTTC84">Generate Special Exemption</button></td>
          <td><button type="button" class="btn btn-primary" @click="generateOvertimeForm">Generate Overtime Form</button></td>
        </tr>
      </table>
    </div>
    </div>
    


  </div>
  
</template>

<script>
import axios from 'axios';
import * as SweetAlertConnector from '../composables/SweetAlertConnector';
import * as PackageTypes from '@/composables/PackageTypes';
import * as DatabaseConnector from '@/composables/DatabaseConnector';
import * as Utilities from '@/composables/Utilities';
import CustomsEntryForm from './CustomsEntry/CustomsEntryForm.vue';
import * as Currency from '@/composables/Currency';


export default {
  name: 'EntryDetails',
  components: {
    CustomsEntryForm
  },
  props: {
    entryId: {
      type: [String, Number],
      required: true
    }
  },
  computed: {
    totalFOBValue() {
      return this.commercialInvoices.reduce((acc, invoice) => acc + parseFloat(invoice.invoice_total), 0).toFixed(2);
    },
    toalInsuranceValue() {
      return this.commercialInvoices.reduce((acc, invoice) => acc + parseFloat(invoice.insurance), 0).toFixed(2);
    },
    formattedDate: {
      get() {
        const date = new Date(this.entryDetails.waybill_date);
        return date.toISOString().split('T')[0];
      },
      set(newValue) {
        this.entryDetails.waybill_date = newValue;
      }
    },
    isGenerateCsvDisabled() {
      return this.commercialInvoices.some(invoice => {
        console.log("Tariff Check" + invoice);
        return invoice.lines.some(line => !line.tariff_code || line.tariff_code.trim() === '');
      });
    },
    availableCPCCodes(){
      if(this.customsEntryForm.regimeTypeCode !== undefined && this.customsEntryForm.regimeTypeCode !== null){
        const regimeData = this.customsEntryForm.regimeTypeOptions.find(regime => regime.regimeTypeCode  === this.customsEntryForm.regimeTypeCode);
        if(regimeData){
          return regimeData.cpcCodes
        } else return []
      }
      else{
        return this.customsEntryForm.regimeTypeOptions.flatMap(regime => regime.cpcCodes)
      }
    }
  },
  data() {
    return {
      entryDetails: null,
      waybill: {},
      entry: {},
      commercialInvoices: [],
      dataRetrieved: false,
      loading: false,
      error: null,
      countries: [],
      totalNumberOfLines: 0,
      modeOfTransportOOptions: ['Air', 'Ocean'],
      freightTypeOptions: ['FCL', 'LCL', 'Bulk', 'Break Bulk'],
      cpcCodeOptions: ['4000'],
      npcCodeOptions: ['0000'],
      packageTypeOptions: [
      ],
      currencies: [],
      customsEntryForm: {
        exporters: [],
            importers: [],
            vessels: [],
            ports: [],
            declarants: [],
            incotermsList: [],
            importerId: '',
            exporterId: '',
            importerName: '',
            exporterName: '',
            importerTaxIdentificationNumber: '',
            exporterTaxIdentificationNumber: '',
            exporterCountry: '',
            declarantId: '',
            declarantTaxIdentificationNumber: '',
            modeOfTransport: '',
            freightType: '',
            waybillNumber: '',
            waybillDate: '',
            vesselId: '',
            vesselName: '',
            incoTerms: '',
            portOfDischargeId: '',
            portOfDischargeName: '',
            netWeight: 0.0,
            grossWeight: 0.0,
            numberOfPackages: 0,
            packageType: '',
            packageTypes: [],
            marksAndNumbers: 'As Addressed',
            totalCargoValue: 0.0,
            freightAmount: 0.0,
            rateOfExchange: 0.0,
            regimeType: '',
            regimeTypeCode: '',
            regimeTypeOptions: [],
            depositAmount: 0.0,
            containerCharges: 0.0,
            additionalCharges: 0.0,
            localFee: 0.0,
            countryOfOrigin: '',
      }
    };
  },
  mounted() {
    this.fetchAllDetails();
    this.fetchCountries();
    this.packageTypeOptions = PackageTypes.usePackageTypes();
    this.currencies = Currency.getCurrencyList();

  },
  methods: {
    setInvoiceLineWorksheetData(){
      let promises = [];
      this.commercialInvoices.forEach(invoice => {
        invoice.lines.forEach(item => {
          item.cpcCodeName = this.availableCPCCodes.find(cpc => cpc.cpcId === item.cpcCode)?.code
          const promise = DatabaseConnector.getCommodityCharges(item.tariff_code.replace(/\./g, '')).then(chargesResponse => {
            let formattedCharges = Utilities.formatCharges(chargesResponse.charges, item.vatApplicable)
            item.commodity = {
              code: item.tariff_code.replace(/\./g, ''),
              id: chargesResponse.id,
              charges: formattedCharges
            }
            // console.log("Commodity Charges: ", item.commodity)
          }).catch(error => {
            SweetAlertConnector.closeCurrentAlert();
            SweetAlertConnector.showErrorAlert(`Error with commodity code provided: ${item.tariff_code}`);
            console.error('Failed to get commodity charges:', error);
            return Promise.reject(error);
            
          });
          promises.push(promise)
        })
      })

      return Promise.all(promises)
    },
    generateWorkSheet(){
      console.log("Generating WorkSheet")
      console.log(this.commercialInvoices)
      SweetAlertConnector.showLoadingAlert('Generating worksheet...');
      this.setInvoiceLineWorksheetData().then(() => {
        console.log("Setting Invoice Line Worksheet Data")
        const worksheetObject = Utilities.formatWorkSheetData(this.entryDetails?.entry_number, 'TT', 'Ramnarine Rampersad', 'BR0285', this.customsEntryForm.regimeTypeCode, this.customsEntryForm.importerName, this.customsEntryForm.importerTaxIdentificationNumber, this.customsEntryForm.exporterName, this.customsEntryForm.exporterTaxIdentificationNumber, this.customsEntryForm.vesselName, this.customsEntryForm.portOfDischargeName, this.customsEntryForm.depositAmount, this.customsEntryForm.containerCharges, this.customsEntryForm.additionalCharges, this.customsEntryForm.localFee, this.customsEntryForm.freightAmount);
        const invoiceSummary = Utilities.formatCommercialData(this.commercialInvoices, this.customsEntryForm.freightAmount, this.totalFOBValue);
        worksheetObject.invoices = invoiceSummary;
        console.log("Worksheet Object: ", worksheetObject)
        DatabaseConnector.generateWorkSheet(worksheetObject).then((worksheetResponse) => {
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('Worksheet Generated Successfully!');
          const fileURL = window.URL.createObjectURL(new Blob([worksheetResponse], { type: 'application/pdf' }));
          const fileLink = document.createElement('a');
          const fileName = `Worksheet - ${this.entryDetails?.entry_number}.pdf`;
          fileLink.href = fileURL;
          fileLink.setAttribute('download', fileName);
          document.body.appendChild(fileLink);
          fileLink.click();
          fileLink.remove();
        }).catch(error => {
          console.error('Failed to generate worksheet:', error);
          SweetAlertConnector.showErrorAlert('Failed to generate worksheet!');
        });
      }).catch(error => {
        console.error('Failed to set invoice line worksheet data:', error);
      });

    },
    async getPackageTypes(){
      try {
        const response = await fetch('./assets/packageTypes.json');
        const data = await response.json();
        this.packageTypeOptions = data;
      } catch (error) {
        this.packageTypeOptions = [
          {value: 'CT', text: 'Carton'},
        ];
        console.error('Error fetching data:', error);
      }
    },
    getInvoiceNumbers(){
      return this.commercialInvoices.map(invoice => {
        return `${invoice.invoice_number} \t ${invoice.invoice_date}`
      }).join('\n');
    },
    async generateOvertimeForm(){
      let overtimeObject = {
          "date":  Utilities.getCurrentDateString(),
          "request": "TO OBTAIN SERVICES OF ONE CUSTOMS OFFICER TO EXAMINE AND RELEASE",
          "details": "",
          "shipper": this.customsEntryForm.exporterName,
          "importer": this.customsEntryForm.importerName,
          "referenceNumber": this.entryDetails?.entry_number,
          "importerAddress": this.customsEntryForm.importerName,
          "declarantNumber": "BR0285",
          "declarantName": "RAMNARINE RAMPERSAD",
          "port": this.customsEntryForm.portOfDischargeName,
      }

      SweetAlertConnector.overtimeAlert().then((response) => {
        console.log("Importers List: ", this.customsEntryForm.importers)
        const selectedImporter = this.customsEntryForm.importers.find(importer => importer.id === this.customsEntryForm.importerId);
        const importerAddress = selectedImporter === undefined ? '' : selectedImporter.address;
        overtimeObject.details = response;
        overtimeObject.importerAddress = `${this.customsEntryForm.importerName}\n${importerAddress}`;
        
        SweetAlertConnector.showLoadingAlert('Generating Overtime Form...');
        DatabaseConnector.generateOvertimeForm(overtimeObject).then((response) => {
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('Overtime Form Generated Successfully!');
          const fileURL = window.URL.createObjectURL(new Blob([response], { type: 'application/pdf' }));
          const fileLink = document.createElement('a');
          const fileName = `Overtime Form - ${this.entryDetails?.entry_number}.pdf`;
          fileLink.href = fileURL;
          fileLink.setAttribute('download', fileName);
          document.body.appendChild(fileLink);
          fileLink.click();
          fileLink.remove();
        }).catch(error => {
          console.error('Failed to generate Overtime Form:', error);
          SweetAlertConnector.showErrorAlert('Failed to generate Overtime Form!');
        });
      })
    },
    async generateTTC84(){
      console.log("Generating TTC84")
      let c84Obj = {
          "declarationNumber": this.entryDetails?.entry_number,
          "declarationDate": Utilities.getCurrentDateString(),
          "regimeCode": "C4",
          "declarantName": "RAMNARINE RAMPERSAD",
          "declarantNumber": "BR0285",
          "referenceNumber": this.entryDetails?.entry_number,
          "importer": this.customsEntryForm.importerName,
          "importNumber": "",
          "declarationContent": '',
          "CPCCode": "4000",
          "extensionCode": "",
          "signatoryName": "",
          "signatoryStatus": "",
          "presigned": false

      }
      const selectedImporter = this.customsEntryForm.importers.find(importer => importer.id === this.customsEntryForm.importerId);
      const importerTaxIdentificationNumber = selectedImporter?.taxIdentificationNumber;
