          if(!item.country_of_origin || item.country_of_origin.trim().length === 0 || item.country_of_origin === 'N/A' || item.country_of_origin === undefined || item.country_of_origin === null){
            item.country_of_origin = this.entryDetails.country_of_origin
          }
